.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.App button {
  margin: 0 .2rem;
  padding: 1rem;
  border: 2px solid transparent;
  border-radius: .5rem;
  background-color: paleturquoise; }

.App button.active {
  border: 2px solid darkcyan; }

.App .track-feed {
  font-size: 1.5rem; }

.App .App-mode {
  display: block;
  position: absolute;
  top: 2px;
  right: 2px; }

.App .App-content {
  display: flex;
  flex-direction: column; }

textarea,
button,
input.text,
input[type="text"],
input[type="button"],
input[type="submit"],
.input-checkbox {
  -webkit-appearance: none;
  border-radius: 0; }
